.education-section {
    padding: 40px 20px;
    background-color: #fff;
  }
  
  .education-header {
    margin-bottom: 40px;
  }
  .education-title {
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 25px;
  }
  
  .education-card {
    border: none;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
  }
  
  .education-card-header {
    background-color: #343a40;
    color: #f8f9fa;
    border-bottom: none;
    border-radius: 10px 10px 0 0;
    text-align: center;
  }
  
  .card-body {
    background-color: #f4f4f4;
    color: #333;
    padding: 20px;
    flex-grow: 1;
    border-radius: 0 0 10px 10px;
  }
  
  .card-title {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  .card-subtitle {
    font-size: 1rem;
    margin-bottom: 15px;
  }
  
  .card-text {
    font-size: 1rem;
    color: #666;
  }

  /* .bg-secondary {
    background-color: #6b46c1;
} */
  
  .card-text strong {
    color: #000;
  }
  
  .key-courses {
    display: flex;
    flex-wrap: wrap;
  }
  
  .badge {
    font-size: 0.875rem;
    padding: 0.5em 0.75em;
    margin-right: 0.5em;
    margin-bottom: 0.5em;
  }
  .white-text {
    color: #ffffff;
}

  
